import { useContext, useState } from "react";
import { Uploader as UploaderModule } from "uploader";
import { UploadButton } from "react-uploader";
import { ToastrContext } from "./ToastrProvider";

// Get production API keys from Upload.io
const uploader = UploaderModule({
  apiKey: "free",
});

// Render the file upload button:
const Uploader = (props: any) => {
  const [imageUrl, setImageUrl] = useState(props.data);
  const notify = useContext(ToastrContext);
  return (
    <UploadButton
      uploader={uploader}
      onComplete={(file: any) => {
        if (file.length === 0) {
          notify.error("No file selected.");
        } else {
          setImageUrl(file[0].fileUrl);
          props.onComplete(file[0].fileUrl);
          notify.success("File uploaded!");
        }
      }}
    >
      {({ onClick }) => (
        <div>
          {imageUrl == "" ? (
            <button
              className="text-center bg-sky-500 rounded-md text-sm font-semibold text-white hover:bg-sky-600 w-full p-2"
              onClick={onClick}
            >
              Upload a file...
            </button>
          ) : (
            <div onClick={onClick}>
              <img
                src={imageUrl}
                className="w-20 h-20 cursor-pointer mx-auto"
              ></img>
            </div>
          )}
        </div>
      )}
    </UploadButton>
  );
};
export default Uploader;
