import { useContext } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { ToastrContext } from "./ToastrProvider";

const Clipboard = (props: any) => {
  const notify = useContext(ToastrContext);
  return (
    <CopyToClipboard
      text={props.text}
      onCopy={() => {
        notify.success("Copied successfully");
      }}
    >
      <div className={"cursor-pointer hover:text-sky-400 " + props.className}>
        {props.icon ? (
          <props.icon className="w-4 h-4"></props.icon>
        ) : (
          props.description ?? props.text
        )}
      </div>
    </CopyToClipboard>
  );
};

export default Clipboard;
