import React, { useContext, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAsk, setModalProject } from "../../store/modal.slice";
import "react-toastify/dist/ReactToastify.css";
import { ToastrContext } from "../../components/ToastrProvider";
import { PlusCircleIcon } from "@heroicons/react/20/solid";
import {
  LinkIcon,
  PencilSquareIcon,
  MagnifyingGlassIcon,
  TrashIcon,
} from "@heroicons/react/24/outline";
import Clipboard from "../../components/Clipboard";
import axios from "../../components/axios";
import { setProject } from "../../store/main.slice";
import Iconify from "../../components/Iconify";
import CopyToClipboard from "react-copy-to-clipboard";

const Project = () => {
  const [searchIndex, setSearchIndex] = useState("");
  const dispatch = useDispatch();
  const notify = useContext(ToastrContext);
  const projects = useSelector((state: any) => state.main.project);
  const setOpen = (e: any) => {
    dispatch(setModalProject(e));
  };
  const handleDelete = async (id: any) => {
    const result: any = await axios.delete(`/api/project/${id}`);
    dispatch(
      setProject(
        projects.filter((i: any) => {
          return i._id !== id;
        })
      )
    );
  };
  useEffect(() => {
    (async () => {
      const result: any = await axios.get("/api/project");
      dispatch(setProject(result.data));
    })();
    return () => {};
  }, []);

  return (
    <>
      <div className="flex justify-between items-center">
        <div className="relative rounded-md shadow-sm w-full">
          <input
            type="text"
            value={searchIndex}
            onChange={(e) => {
              setSearchIndex(e.target.value.toLowerCase());
            }}
            name="search-project"
            id="search-project"
            className="block bg-transparent w-full rounded-md border-0 py-1.5 pr-10 outline-none px-2 ring-1 ring-inset ring-gray-300 dark:ring-indigo-900 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            placeholder="Search..."
          />
          <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-3">
            <MagnifyingGlassIcon
              className="h-5 w-5 text-gray-400"
              aria-hidden="true"
            />
          </div>
        </div>
        <button
          type="button"
          onClick={() => {
            setOpen({
              state: "open",
              data: "",
            });
          }}
          className="flex flex-none ml-5 justify-center items-center gap-x-1.5 rounded-md bg-indigo-600  p-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
        >
          <PlusCircleIcon className="h-5 w-5" aria-hidden="true" /> Add New
        </button>
      </div>
      <div className="mt-4 w-full flex gap-4 md:overflow-auto md:flex-row flex-col">
        {projects
          .filter((item: any) => {
            return (
              item.title.toLowerCase().includes(searchIndex) ||
              item.builder.toLowerCase().includes(searchIndex) ||
              item.stack.toLowerCase().includes(searchIndex) ||
              item.description.toLowerCase().includes(searchIndex)
            );
          })
          .map((item: any, id: any) => {
            return (
              <div
                className="mb-2 w-full md:w-[300px] relative flex-none"
                key={"project-view-" + id}
              >
                <div className="relative border border-indigo-400/30 bg-indigo-100/10  md:h-[calc(100vh-220px)] md:overflow-auto">
                  <img src={item.image} className="aspect-[2/1] w-full" />
                  <div className="flex flex-col gap-2 p-2 text-sm overflow-hidden">
                    <div className="text-lg text-indigo-400">{item.title}</div>
                    <div className="flex items-center gap-2">
                      <Iconify
                        icon={"fontisto:world-o"}
                        classes="w-4 h-4 cursor-pointer flex-none"
                      ></Iconify>
                      <Clipboard
                        text={item.url}
                        className="m-overflow"
                      ></Clipboard>
                      <a
                        href={item.url}
                        className="ml-auto flex-none"
                        target="_blank"
                      >
                        <LinkIcon className="w-4 h-4 hover:text-red-400"></LinkIcon>
                      </a>
                    </div>
                    <div className="flex items-center gap-2">
                      <Iconify
                        icon={"radix-icons:github-logo"}
                        classes="w-4 h-4 cursor-pointer flex-none"
                      ></Iconify>
                      <Clipboard
                        text={item.github}
                        className="m-overflow"
                      ></Clipboard>
                      <a
                        href={item.github}
                        className="ml-auto flex-none"
                        target="_blank"
                      >
                        <LinkIcon className="w-4 h-4 hover:text-red-400"></LinkIcon>
                      </a>
                    </div>

                    <div className="flex items-center gap-2">
                      <Iconify icon={"entypo:user"} classes="w-4 h-4"></Iconify>
                      <div className="m-overflow">{item.builder} </div>
                    </div>
                    <div className="">{item.stack}</div>
                    <div className="border-t border-gray-400/40 pt-4">
                      {item.description}
                    </div>
                  </div>
                </div>
                <div className="absolute top-2 right-2 flex gap-3 py-2 px-3 items-center bg-gray-800/80 text-gray-300 rounded-md">
                  <CopyToClipboard
                    text={item.url}
                    onCopy={() => {
                      notify.success("Copied successfully");
                    }}
                  >
                    <LinkIcon className="w-4 h-4 flex-none cursor-pointer hover:text-yellow-400 "></LinkIcon>
                  </CopyToClipboard>
                  <TrashIcon
                    className="w-4 h-4 flex-none cursor-pointer hover:text-red-400 "
                    onClick={() => {
                      dispatch(
                        setAsk({
                          state: true,
                          callback: () => {
                            handleDelete(item._id);
                            notify.success("Deleted successfully");
                          },
                        })
                      );
                    }}
                  ></TrashIcon>
                  <PencilSquareIcon
                    className="w-4 h-4 flex-none cursor-pointer hover:text-sky-400 "
                    onClick={() => {
                      setOpen({
                        state: item._id,
                        data: "",
                      });
                    }}
                  ></PencilSquareIcon>
                </div>
              </div>
            );
          })}
      </div>
    </>
  );
};

export default Project;
