import React, { useState } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import {
  CheckIcon,
  PencilSquareIcon,
  XMarkIcon,
} from "@heroicons/react/24/outline";
import Iconify from "./Iconify";
const ClipboardInput = (props: any) => {
  const [edit, setEdit] = useState(false);
  const [inputValue, setInputValue] = useState(props.text);
  return (
    <div className="flex items-center">
      {props.icon && (
        <Iconify icon={props.icon} classes={"w-8 h-8 p-1  text-indigo-400"} />
      )}
      {props.description && (
        <div className=" font-semibold mr-1 text-indigo-400">
          {props.description}
        </div>
      )}
      {!edit ? (
        <div className="flex gap-2 items-center">
          <CopyToClipboard
            text={props.text}
            onCopy={() => {
              props.onCopy();
            }}
          >
            <div className="pl-2 cursor-pointer hover:text-sky-400">
              {props.text}
            </div>
          </CopyToClipboard>
          <PencilSquareIcon
            className="w-5 cursor-pointer  hover:text-sky-400"
            onClick={() => {
              setEdit(true);
            }}
          ></PencilSquareIcon>
        </div>
      ) : (
        <div className="relative  w-[240px]">
          <input
            value={inputValue}
            onChange={(e) => {
              setInputValue(e.target.value);
            }}
            className=" bg-transparent border-b-2 border-sky-400 outline-none pl-2 pr-14"
          ></input>
          <div className="flex absolute inset-y-0 right-0 gap-1">
            <CheckIcon
              className="w-5 cursor-pointer text-sky-400"
              onClick={() => {
                props.onChange(inputValue);
                setEdit(false);
              }}
            ></CheckIcon>
            <XMarkIcon
              className="w-5 cursor-pointer text-red-400"
              onClick={() => {
                setInputValue(props.text);
                setEdit(false);
              }}
            ></XMarkIcon>
          </div>
        </div>
      )}
    </div>
  );
};

export default ClipboardInput;
