import React, { useContext, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAsk, setModalXp } from "../../store/modal.slice";
import ClipboardInput from "../../components/ClipboardInput";
import "react-toastify/dist/ReactToastify.css";
import { ToastrContext } from "../../components/ToastrProvider";
import {
  PlusCircleIcon,
  CalendarIcon,
  MapPinIcon,
} from "@heroicons/react/20/solid";
import { PencilSquareIcon, TrashIcon } from "@heroicons/react/24/outline";
import axios from "../../components/axios";
import { setExperience, setProfile } from "../../store/main.slice";

const Profile = () => {
  const notify = useContext(ToastrContext);
  const accountData = useSelector((state: any) =>
    state.main.profile.filter((i: any) => {
      return i.type == "profile";
    })
  );
  const backgroundData = useSelector((state: any) =>
    state.main.profile.filter((i: any) => {
      return i.type == "background";
    })
  );
  const xpData = useSelector((state: any) => state.main.experience);
  const dispatch = useDispatch();
  const setOpen = (e: any) => {
    dispatch(setModalXp(e));
  };
  const handleXpDelete = async (id: any) => {
    const result: any = await axios.delete(`/api/experience/${id}`);
    dispatch(
      setExperience(
        xpData.filter((i: any) => {
          return i._id !== id;
        })
      )
    );
  };
  const handleProfileUpdate = async (id: any, text: any) => {
    const result = await axios.put(`/api/profile/${id}`, {
      text,
    });
    dispatch(setProfile(result.data));
    notify.success("Updated successfully!");
  };
  useEffect(() => {
    (async () => {
      const result: any = await axios.get("/api/profile");
      dispatch(setProfile(result.data));
    })();
    (async () => {
      const result: any = await axios.get("/api/experience");
      dispatch(setExperience(result.data));
    })();
    return () => {};
  }, []);

  return (
    <>
      <div className="flex flex-col gap-8 md:gap-8 lg:gap-12 xl:gap-16 md:flex-row justify-evenly">
        <div className="min-w-[300px]">
          <div className="flex flex-col gap-2.5">
            <h1 className="text-3xl text-indigo-400 font-semibold py-4">
              PROFILE
            </h1>
            {accountData.map((item: any) => {
              return (
                <ClipboardInput
                  key={item.title}
                  icon={item.icon}
                  text={item.text}
                  onChange={(data: any) => {
                    handleProfileUpdate(item._id, data);
                  }}
                  onCopy={() => {
                    notify.success("Copied successfully!", 100);
                  }}
                ></ClipboardInput>
              );
            })}
          </div>
          <div className="flex flex-col gap-2.5 mt-8">
            <h1 className="text-3xl text-indigo-400 font-semibold py-4">
              BACKGROUND
            </h1>
            {backgroundData.map((item: any) => {
              return (
                <ClipboardInput
                  key={item.title}
                  description={item.icon}
                  text={item.text}
                  onChange={(data: any) => {
                    alert(data);
                  }}
                  onCopy={() => {
                    notify.success("Copied!", 100);
                  }}
                ></ClipboardInput>
              );
            })}
          </div>
        </div>
        <div className="mt-8 md:mt-0">
          <h1 className="text-3xl text-indigo-400 font-semibold py-4 uppercase">
            Education and Work Experience
          </h1>
          {xpData.map((item: any, id: any) => {
            return (
              <div
                key={"xppos" + id}
                className="dark:bg-indigo-900 p-4 rounded-md bg-indigo-100 mb-6"
              >
                <div className="text-2xl font-semibold border-b p-1.5 border-indigo-400/20 flex justify-between items-start">
                  {item.title}
                  <TrashIcon
                    className="w-5 h-5 flex-none cursor-pointer hover:text-red-400 ml-auto mr-2"
                    onClick={() => {
                      dispatch(
                        setAsk({
                          state: true,
                          callback: () => {
                            handleXpDelete(item._id);
                            notify.success("Deleted successfully");
                          },
                        })
                      );
                    }}
                  ></TrashIcon>
                  <PencilSquareIcon
                    className="w-5 h-5 flex-none cursor-pointer hover:text-sky-400"
                    onClick={() => {
                      setOpen(item._id);
                    }}
                  ></PencilSquareIcon>
                </div>
                <div className="p-2">
                  <div className="font-semibold text-lg">{item.role}</div>
                  <div className="mb-2">
                    <div className=" inline-flex items-center gap-1">
                      <MapPinIcon
                        className="-ml-0.5 h-5 w-5"
                        aria-hidden="true"
                      />
                      {item.position}
                    </div>
                    <div className=" inline-flex items-center gap-1 ml-4">
                      <CalendarIcon
                        className="-ml-0.5 h-5 w-5"
                        aria-hidden="true"
                      />
                      {item.duration}
                    </div>
                  </div>
                  {item.description}
                </div>
              </div>
            );
          })}
          <div className="flex justify-center">
            <button
              type="button"
              onClick={() => {
                setOpen("open");
              }}
              className="inline-flex items-center gap-x-1.5 rounded-md bg-indigo-600 px-6 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              <PlusCircleIcon className="-ml-0.5 h-5 w-5" aria-hidden="true" />
              Add New
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Profile;
