import { Fragment, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Dialog, Transition } from "@headlessui/react";
import { setAsk } from "../store/modal.slice";

export default function ModalAsk() {
  const ask = useSelector((state: any) => state.modals.ask);
  const dispatch = useDispatch();
  const cancelButtonRef = useRef(null);
  const handleClose = () => {
    dispatch(setAsk({ ...ask, state: false }));
  };
  const handleOk = () => {
    ask.callback();
    handleClose();
  };
  return (
    <Transition.Root show={ask.state} as={Fragment}>
      <Dialog
        as="div"
        className="relative z-[1000]"
        initialFocus={cancelButtonRef}
        onClose={handleClose}
      >
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
        </Transition.Child>

        <div className="fixed inset-0 z-[1000] overflow-y-auto">
          <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              enterTo="opacity-100 translate-y-0 sm:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 sm:scale-100"
              leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
            >
              <Dialog.Panel className=" relative transform overflow-hidden rounded-lg bg-white dark:bg-indigo-900 px-4 pb-4 pt-5 text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg sm:p-6">
                <div className="max-h-[80vh] overflow-auto">
                  <Dialog.Title
                    as="h3"
                    className="text-2xl font-semibold leading-6 mt-3 text-center sm:mt-5"
                  >
                    Delete
                  </Dialog.Title>
                  <div className="mt-5 text-center">
                    Do you want to delete this data?
                  </div>
                  <div className="mt-5 sm:mt-6 flex sm:gap-3">
                    <button
                      type="button"
                      className="w-1/2  outline-none justify-center rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600 "
                      onClick={handleOk}
                    >
                      OK
                    </button>
                    <button
                      type="button"
                      className="w-1/2 outline-none  justify-center rounded-md  px-3 py-2 text-sm font-semibold text-gray-900 dark:text-white shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50/10"
                      onClick={handleClose}
                      ref={cancelButtonRef}
                    >
                      Cancel
                    </button>
                  </div>
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition.Root>
  );
}
