import Dashboard from "../pages/Dashboard";
import Profile from "../pages/Profile";
import Interview from "../pages/Interview";
import Asset from "../pages/Asset";
import Project from "../pages/Project";
import Address from "../pages/Address";
import {
  UserIcon,
  UserGroupIcon,
  BriefcaseIcon,
  DocumentDuplicateIcon,
  FolderIcon,
  HomeIcon,
} from "@heroicons/react/24/outline";
const userRoutes = [
  {
    url: "/dashboard",
    component: Dashboard,
    index: true,
    icon: HomeIcon,
    text: "Dashboard",
  },
  {
    url: "/profile",
    component: Profile,
    index: true,
    icon: UserIcon,
    text: "Profile",
  },
  {
    url: "/interview",
    component: Interview,
    index: true,
    icon: DocumentDuplicateIcon,
    text: "Interview",
  },
  {
    url: "/assets",
    component: Asset,
    index: true,
    icon: FolderIcon,
    text: "Assets",
  },
  {
    url: "/projects",
    component: Project,
    index: true,
    icon: BriefcaseIcon,
    text: "Projects",
  },
  {
    url: "/addressbook",
    component: Address,
    index: true,
    icon: UserGroupIcon,
    text: "Adresses",
  },
];
export default userRoutes;
