import React from "react";
import { Provider } from "react-redux";
import { store } from "./app/store";
import RouteComponent from "./app/routes";
import ToastrProvider from "./app/components/ToastrProvider";

const App = () => {
  return (
    <React.StrictMode>
      <Provider store={store}>
        <ToastrProvider>
          <RouteComponent />
        </ToastrProvider>
      </Provider>
    </React.StrictMode>
  );
};

export default App;
