import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  xp: "closed",
  ask: { state: false, callback: null },
  interview: { state: "closed", data: "" },
  asset: { state: "closed", data: "" },
  project: { state: "closed", data: "" },
  address: { state: "closed", data: "" },
};

export const slice = createSlice({
  name: "modals",
  initialState,
  reducers: {
    setModalXp: (state, action) => {
      state.xp = action.payload;
    },
    setAsk: (state, action) => {
      state.ask = action.payload;
    },
    setModalInterview: (state, action) => {
      state.interview = action.payload;
    },
    setModalAsset: (state, action) => {
      state.asset = action.payload;
    },
    setModalProject: (state, action) => {
      state.project = action.payload;
    },
    setModalAddress: (state, action) => {
      state.address = action.payload;
    },
  },
});

export const {
  setModalXp,
  setAsk,
  setModalInterview,
  setModalAsset,
  setModalProject,
  setModalAddress,
} = slice.actions;

export default slice.reducer;
