import { Fragment, useRef, useState, useEffect, useContext } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Dialog, Transition } from "@headlessui/react";
import { setModalAsset } from "../store/modal.slice";
import axios from "../components/axios";
import { setAsset } from "../store/main.slice";
import { ToastrContext } from "../components/ToastrProvider";
import Uploader from "../components/Uploader";

export default function ModalAsset() {
  const notify = useContext(ToastrContext);
  const assets = useSelector((state: any) => state.main.asset);
  const open = useSelector((state: any) => state.modals.asset);
  const [values, setValues] = useState({
    text: "",
    url: "",
    parent: "",
    image: "",
  });
  const dispatch = useDispatch();
  const handleSave = async () => {
    if (!values.image) {
      notify.error("Please upload an image");
      return;
    }
    if (open.state === "open") {
      const result = await axios.post("/api/asset", values);
      if (result.data) dispatch(setAsset([...assets, result.data]));
      notify.success("Created successfully!");
    } else if (open.state !== "closed") {
      const result = await axios.put(`/api/asset/${open.state}`, values);
      if (result.data)
        dispatch(
          setAsset(
            assets.map((i: any) => {
              return i._id === open.state ? result.data : i;
            })
          )
        );
      notify.success("Updated successfully!");
    }
    onClose();
  };
  const setOpen = (e: any) => {
    dispatch(setModalAsset({ ...open, state: e }));
  };
  const onClose = () => {
    setOpen("closed");
  };
  useEffect(() => {
    if (open.state == "open")
      setValues({ text: "", url: "", parent: open.data, image: "" });
    else if (open.state != "closed")
      setValues(
        assets.find((i: any) => {
          return i._id == open.state;
        })
      );
    return () => {};
  }, [open]);

  const cancelButtonRef = useRef(null);
  return (
    <Transition.Root show={open.state != "closed"} as={Fragment}>
      <Dialog
        as="div"
        className="relative z-[1000]"
        initialFocus={cancelButtonRef}
        onClose={onClose}
      >
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
        </Transition.Child>

        <div className="fixed inset-0 z-[1000] overflow-y-auto">
          <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              enterTo="opacity-100 translate-y-0 sm:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 sm:scale-100"
              leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
            >
              <Dialog.Panel className=" relative transform overflow-hidden rounded-lg bg-white dark:bg-indigo-900 px-4 pb-4 pt-5 text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg sm:p-6">
                <div className="max-h-[80vh] overflow-auto">
                  <Dialog.Title
                    as="h3"
                    className="text-2xl font-semibold leading-6 mt-3 text-center sm:mt-5"
                  >
                    Asset
                  </Dialog.Title>

                  <div className=" relative mt-4">
                    <label className="absolute -top-2 left-2 inline-block bg-white dark:bg-indigo-900 px-1 text-xs uppercase font-medium">
                      Text
                    </label>
                    <input
                      type="text"
                      value={values.text}
                      name={"text"}
                      id={"text"}
                      onChange={(event) => {
                        setValues({
                          ...values,
                          text: event.target.value,
                        });
                      }}
                      className="block w-full bg-transparent px-4 outline-none rounded-md border-0 py-1.5 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-400 sm:text-sm sm:leading-6"
                    />
                  </div>
                  <div className="mt-4 relative">
                    <label className="absolute -top-2 left-2 inline-block bg-white dark:bg-indigo-900 px-1 text-xs uppercase font-medium">
                      url
                    </label>
                    <input
                      name="url"
                      value={values.url}
                      onChange={(e) => {
                        setValues({ ...values, url: e.target.value });
                      }}
                      id="url"
                      className="block w-full outline-none p-4 rounded-md border-0 py-1.5 bg-transparent shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>

                  <div className=" relative mt-4">
                    <label className="absolute -top-2 left-2 inline-block bg-white dark:bg-indigo-900 px-1 text-xs uppercase font-medium">
                      Parent
                    </label>
                    <input
                      type="text"
                      value={values.parent}
                      name={"parent"}
                      id={"parent"}
                      onChange={(event) => {
                        setValues({
                          ...values,
                          parent: event.target.value,
                        });
                      }}
                      className="block w-full bg-transparent px-4 outline-none rounded-md border-0 py-1.5 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-400 sm:text-sm sm:leading-6"
                    />
                  </div>
                  <div className="mt-5">
                    <Uploader
                      data={values.image}
                      onComplete={(e: any) => {
                        setValues({ ...values, image: e });
                      }}
                    ></Uploader>
                  </div>
                  <div className="mt-5 sm:mt-6 flex sm:gap-3">
                    <button
                      type="button"
                      className="w-1/2 justify-center outline-none rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600 "
                      onClick={handleSave}
                    >
                      Save
                    </button>
                    <button
                      type="button"
                      className="w-1/2 justify-center outline-none rounded-md  px-3 py-2 text-sm font-semibold text-gray-900 dark:text-white shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50/10"
                      onClick={onClose}
                      ref={cancelButtonRef}
                    >
                      Cancel
                    </button>
                  </div>
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition.Root>
  );
}
