import React, { useContext, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAsk, setModalInterview } from "../../store/modal.slice";
import "react-toastify/dist/ReactToastify.css";
import { ToastrContext } from "../../components/ToastrProvider";
import { PlusCircleIcon } from "@heroicons/react/20/solid";
import {
  LinkIcon,
  PencilSquareIcon,
  TrashIcon,
} from "@heroicons/react/24/outline";
import axios from "../../components/axios";
import { setInterview, setParent } from "../../store/main.slice";

const Interview = () => {
  const [selectedId, setSelectedId] = useState(0);
  const [selectedParentId, setSelectedParentId] = useState(0);
  const dispatch = useDispatch();
  const notify = useContext(ToastrContext);
  const interviewParents = useSelector((state: any) =>
    state.main.parent.filter((item: any) => {
      return item.type === "interview";
    })
  );
  const interviews = useSelector((state: any) => state.main.interview);
  const setOpen = (e: any) => {
    dispatch(setModalInterview(e));
  };
  const handleXpDelete = async (id: any) => {
    const result: any = await axios.delete(`/api/interview/${id}`);
    dispatch(
      setInterview(
        interviews.filter((i: any) => {
          return i._id !== id;
        })
      )
    );
  };
  useEffect(() => {
    (async () => {
      const result: any = await axios.get("/api/parent");
      dispatch(setParent(result.data));
    })();
    (async () => {
      const result: any = await axios.get("/api/interview");
      dispatch(setInterview(result.data));
    })();
    return () => {};
  }, []);

  return (
    <>
      <div className="flex gap-8 items-start flex-col md:flex-row">
        <div className="w-full md:max-w-[240px]">
          <div className="bg-indigo-100 dark:bg-indigo-900 p-2 flex-none md:h-max-[calc(100vh-200px)] overflow-auto shadow-md">
            {interviewParents.map((item: any, id: number) => {
              return (
                <div
                  onClick={() => {
                    setSelectedParentId(id);
                  }}
                  className={
                    "cursor-pointer flex justify-between items-center p-2 " +
                    (id === selectedParentId
                      ? "bg-indigo-200 dark:bg-indigo-600"
                      : "dark:hover:bg-gray-50/10 hover:bg-gray-500/10")
                  }
                  key={"interview-parent-" + id}
                >
                  {item.text}
                </div>
              );
            })}
          </div>
          <button
            type="button"
            onClick={() => {
              setOpen({
                state: "open",
                data: interviewParents[selectedParentId].text,
              });
            }}
            className="flex mt-3 justify-center items-center w-full gap-x-1.5 rounded-md bg-indigo-600 px-6 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
          >
            <PlusCircleIcon className="-ml-0.5 h-5 w-5" aria-hidden="true" />
            Add New
          </button>
        </div>
        <div className="w-full grow">
          {interviews
            .filter((item: any) => {
              return item.parent == interviewParents[selectedParentId].text;
            })
            .map((item: any, id: any) => {
              return (
                <div className="mb-2" key={"interview-view-" + id}>
                  <div
                    onClick={() => {
                      setSelectedId(id);
                    }}
                    className="flex items-center text-lg font-semibold py-2 px-4 bg-indigo-200 dark:bg-indigo-600 cursor-pointer hover:bg-indigo-300 dark:hover:bg-indigo-500"
                  >
                    {item.question}
                    <TrashIcon
                      className="w-5 h-5 flex-none cursor-pointer hover:text-red-400 ml-auto mr-2"
                      onClick={() => {
                        dispatch(
                          setAsk({
                            state: true,
                            callback: () => {
                              handleXpDelete(item._id);
                              notify.success("Deleted successfully");
                            },
                          })
                        );
                      }}
                    ></TrashIcon>
                    <PencilSquareIcon
                      className="w-5 h-5 flex-none cursor-pointer hover:text-sky-400"
                      onClick={() => {
                        setOpen({
                          state: item._id,
                          data: interviewParents[selectedParentId].text,
                        });
                      }}
                    ></PencilSquareIcon>
                  </div>
                  {id === selectedId && (
                    <div className="py-4 px-6  border dark:border-indigo-600  border-indigo-200 ">
                      {item.answer}
                    </div>
                  )}
                </div>
              );
            })}
        </div>
      </div>
    </>
  );
};

export default Interview;
