import React, { useContext, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAsk, setModalAddress } from "../../store/modal.slice";
import "react-toastify/dist/ReactToastify.css";
import { ToastrContext } from "../../components/ToastrProvider";
import { PlusCircleIcon } from "@heroicons/react/20/solid";
import { CopyToClipboard } from "react-copy-to-clipboard";
import {
  LinkIcon,
  PencilSquareIcon,
  PlusIcon,
  TrashIcon,
} from "@heroicons/react/24/outline";
import axios from "../../components/axios";
import Iconify from "../../components/Iconify";
import { setAddress, setParent } from "../../store/main.slice";
import Clipboard from "../../components/Clipboard";
const icons = {
  name: "entypo:user",
  email: "ic:baseline-email",
  address: "material-symbols:location-on-rounded",
  phone: "solar:phone-bold",
  linkedin: "ri:linkedin-box-fill",
  github: "radix-icons:github-logo",
  twitter: "ri:twitter-fill",
  portfolio: "fontisto:world-o",
};
const Address = () => {
  const [selectedParentId, setSelectedParentId] = useState(0);
  const dispatch = useDispatch();
  const notify = useContext(ToastrContext);
  const addressParents = useSelector((state: any) =>
    state.main.parent.filter((item: any) => {
      return item.type === "address";
    })
  );
  const addresses = useSelector((state: any) => state.main.address);
  const setOpen = (e: any) => {
    dispatch(setModalAddress(e));
  };
  const handleDelete = async (id: any) => {
    const result: any = await axios.delete(`/api/address/${id}`);
    dispatch(
      setAddress(
        addresses.filter((i: any) => {
          return i._id !== id;
        })
      )
    );
  };
  useEffect(() => {
    (async () => {
      const result: any = await axios.get("/api/parent");
      dispatch(setParent(result.data));
    })();
    (async () => {
      const result: any = await axios.get("/api/address");
      dispatch(setAddress(result.data));
    })();
    return () => {};
  }, []);

  return (
    <>
      <div>
        <div className="sm:hidden">
          <select
            id="tabs"
            name="tabs"
            value={selectedParentId}
            onChange={(e: any) => {
              setSelectedParentId(e.target.value);
            }}
            className="block w-full bg-transparent border border-gray-400 py-2 pl-3 pr-10 text-base focus:border-indigo-500 focus:outline-none focus:ring-indigo-500 sm:text-sm"
          >
            {addressParents.map((tab: any, id: number) => (
              <option key={"addresstabsm" + tab._id} value={id}>
                {tab.text}
              </option>
            ))}
          </select>
          <button
            type="button"
            onClick={() => {
              setOpen({
                state: "open",
                data: addressParents[selectedParentId].text,
              });
            }}
            className="flex mt-3 w-full justify-center items-center gap-x-1.5 rounded-md bg-indigo-600  p-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
          >
            <PlusCircleIcon className="h-5 w-5" aria-hidden="true" /> Add New
          </button>
        </div>
        <div className="hidden sm:block">
          <div className="border-b border-gray-200 dark:border-indigo-800/50 flex justify-between items-center">
            <nav className="-mb-px flex space-x-8" aria-label="Tabs">
              {addressParents.map((tab: any, id: number) => (
                <a
                  onClick={() => {
                    setSelectedParentId(id);
                  }}
                  key={"addresstabmd" + tab._id}
                  className={
                    (id === selectedParentId
                      ? "border-indigo-500 text-indigo-600"
                      : "border-transparent dark:text-gray-400 text-gray-500 hover:border-gray-300 dark:hover:border-indigo-300 hover:text-gray-700") +
                    "whitespace-nowrap border-b-2 py-4 px-1 text-sm font-medium cursor-pointer"
                  }
                >
                  {tab.text}
                </a>
              ))}
            </nav>
            <button
              type="button"
              onClick={() => {
                setOpen({
                  state: "open",
                  data: addressParents[selectedParentId].text,
                });
              }}
              className="flex justify-center items-center gap-x-1.5 rounded-md bg-indigo-600  p-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              <PlusCircleIcon className="h-5 w-5" aria-hidden="true" />
            </button>
          </div>
        </div>
      </div>
      <div className="mt-4 w-full flex gap-4 md:overflow-auto md:flex-row flex-col">
        {addresses.map((item: any, id: any) => {
          return (
            <div
              className="mb-2 w-full md:w-[250px] relative flex-none"
              key={"address-view-" + id}
            >
              <div className="relative border border-indigo-400/30 bg-indigo-100/10  md:h-[calc(100vh-220px)] md:overflow-auto">
                <img
                  src={item.image}
                  className="aspect-[1/1] w-[60%] mt-5 mb-1 mx-auto rounded-full"
                />
                <div className="flex flex-col gap-2 p-2 text-sm overflow-hidden">
                  <div className="flex items-center gap-2">
                    <Iconify
                      icon={icons.name}
                      classes="w-4 h-4 flex-none"
                    ></Iconify>
                    <Clipboard
                      text={item.name}
                      className="m-overflow"
                    ></Clipboard>
                  </div>

                  <div className="flex items-center gap-2">
                    <Iconify
                      icon={icons.email}
                      classes="w-4 h-4 flex-none"
                    ></Iconify>
                    <Clipboard
                      text={item.email}
                      className="m-overflow"
                    ></Clipboard>
                  </div>
                  <div className="flex items-center gap-2">
                    <Iconify
                      icon={icons.phone}
                      classes="w-4 h-4 flex-none"
                    ></Iconify>
                    <Clipboard
                      text={item.phone}
                      className="m-overflow"
                    ></Clipboard>
                  </div>
                  <div className="flex items-center gap-2">
                    <Iconify
                      icon={icons.address}
                      classes="w-4 h-4 flex-none"
                    ></Iconify>
                    <Clipboard
                      text={item.address}
                      className="m-overflow"
                    ></Clipboard>
                  </div>

                  <div className="flex items-center gap-2">
                    <Iconify
                      icon={icons.linkedin}
                      classes="w-4 h-4 flex-none"
                    ></Iconify>
                    <Clipboard
                      text={item.linkedin}
                      className="m-overflow"
                    ></Clipboard>
                    <a
                      href={item.linkedin}
                      className="ml-auto flex-none"
                      target="_blank"
                    >
                      <LinkIcon className="w-4 h-4 hover:text-red-400"></LinkIcon>
                    </a>
                  </div>
                  <div className="flex items-center gap-2">
                    <Iconify
                      icon={icons.twitter}
                      classes="w-4 h-4 flex-none"
                    ></Iconify>
                    <Clipboard
                      text={item.twitter}
                      className="m-overflow"
                    ></Clipboard>
                    <a
                      href={item.twitter}
                      className="ml-auto flex-none"
                      target="_blank"
                    >
                      <LinkIcon className="w-4 h-4 hover:text-red-400"></LinkIcon>
                    </a>
                  </div>
                  <div className="flex items-center gap-2">
                    <Iconify
                      icon={icons.github}
                      classes="w-4 h-4 flex-none"
                    ></Iconify>
                    <Clipboard
                      text={item.github}
                      className="m-overflow"
                    ></Clipboard>
                    <a
                      href={item.github}
                      className="ml-auto flex-none"
                      target="_blank"
                    >
                      <LinkIcon className="w-4 h-4 hover:text-red-400"></LinkIcon>
                    </a>
                  </div>
                  <div className="flex items-center gap-2">
                    <Iconify
                      icon={icons.portfolio}
                      classes="w-4 h-4 flex-none"
                    ></Iconify>
                    <Clipboard
                      text={item.portfolio}
                      className="m-overflow"
                    ></Clipboard>
                    <a
                      href={item.portfolio}
                      className="ml-auto flex-none"
                      target="_blank"
                    >
                      <LinkIcon className="w-4 h-4 hover:text-red-400"></LinkIcon>
                    </a>
                  </div>
                  <div className="border-t border-gray-400/40 pt-4">
                    {item.description}
                  </div>
                </div>
              </div>
              <div className="absolute top-2 right-2 flex gap-3 py-2 px-3 items-center bg-gray-800/80 text-gray-300 rounded-md">
                <TrashIcon
                  className="w-4 h-4 flex-none cursor-pointer hover:text-red-400 "
                  onClick={() => {
                    dispatch(
                      setAsk({
                        state: true,
                        callback: () => {
                          handleDelete(item._id);
                          notify.success("Deleted successfully");
                        },
                      })
                    );
                  }}
                ></TrashIcon>
                <PencilSquareIcon
                  className="w-4 h-4 flex-none cursor-pointer hover:text-sky-400 "
                  onClick={() => {
                    setOpen({
                      state: item._id,
                      data: "",
                    });
                  }}
                ></PencilSquareIcon>
              </div>
            </div>
          );
        })}
      </div>
    </>
  );
};

export default Address;
